.feuture-wrap {
    width: 100%;
    overflow: hidden;
    @include background("../img/img_bg_product.png", -300px 0, no-repeat, 70%);
    margin-top: 12.5%;

    @include mq($until: wide-1400) {
        @include background("../img/img_bg_product.png", -300px 0, no-repeat, 45%);
    }

    @include mq($until: wide-1281) {
        @include background("../img/img_bg_product.png", -200px 0, no-repeat, 55%);
    }

    @include mq($until: leptop-769) {
        @include background("../img/img_bg_product.png", 200px 0, no-repeat, 90%);
    }

    @include mq($until: mobile-415) {
        @include background("../img/img_bg_product.png", 60px 0px, no-repeat, 100%);
    }


    >.feuture-wrap-inner {
        width: 100%;
        max-width: 1366px;
        margin: 0 auto;
        padding-top: 2rem;

        @include mq($until: mobile-491) {
            padding-top: 0rem;
        }

        @include mq($until: mobile-415) {
            margin-top: 2%;
        }

        >* {
            float: left;
            width: 50%;

            @include mq($until: leptop-769) {
                float: none;
                width: 100%;
            }
        }
    }
}

.is-feuture-img-inner {
    text-align: center;
}

.is-feuture-info-inner {
    margin: auto;
    max-width: 90%;

    @include mq($until: leptop-769) {
        max-width: 80%;
    }

    @include mq($until: mobile-415) {
        max-width: 90%;
    }
}

.is-feuture-img-inner {
    >img {
        @include keyframe(alternate);
        display: block;
        margin-top: 1rem;

        &img.even {
            @include keyframe(alternate-reverse);
        }
    }
}

.is-feuture-img-inner {
    img {
        width: 100%;

        @include mq($until: leptop-769) {
            width: 90%;
        }

        @include mq($until: leptop-641) {
            width: 100%;
        }

    }
}

.is-feuture-img-inner {
    >img:first-child {
        display: block;

        @include mq($until: mobile-415) {
            display: none;
        }
    }

    >img:last-child {
        display: none;

        @include mq($until: mobile-415) {
            display: block;
        }
    }
}

.is-feuture-info-inner {
    width: 100%;
}

.is-feuture-info-1 {
    >p:first-child {
        @include font-style(1.25rem, rgb(33, 76, 161), $line-height-lg, $font-weight-light, $font-spacing: 1px);

        @include mq($until: wide-1081) {
            font-size: 1.15rem;
            letter-spacing: 1px;
        }

        @include mq($until: desktop) {
            font-size: 1rem;
        }

        @include mq($until: leptop-769) {
            font-size: 1.25rem;
            padding-top: 2rem;
        }

        @include mq($until: leptop-641) {
            font-size: 1.15rem;
            letter-spacing: 0px;
        }

        @include mq($until: mobile-541) {
            font-size: 1rem;
        }

        @include mq($until: mobile-415) {
            font-size: .875rem;
            padding-top: 1rem;

        }

        @include mq($until: mobile) {
            font-size: .85rem;
            padding-top: 2rem;
        }
    }

    >p:last-child {
        @include font-style(2.25rem, rgb(33, 76, 161), $line-height-lg * 1.25, $font-weight-bold, $font-spacing: 2px);

        @include mq($until: wide-1081) {
            font-size: 1.75rem;
        }

        @include mq($until: desktop-894) {
            font-size: 1.5rem;
        }

        @include mq($until: mobile-415) {
            font-size: 1.375rem;
        }

        @include mq($until: mobile) {
            font-size: 1rem;
        }
    }
}

.is-feuture-info-2 {
    width: 100%;

    >p:first-child {
        display: inline-block;
        @include font-style(.875rem, rgb(255, 255, 255), $line-height-lg, $font-weight-light, $font-spacing: 0px);
        @include border-radius(3rem);
        @include button-bg(rgb(24, 65, 175));
        padding: .15rem .875rem;

        @include mq($until: mobile-415) {
            font-size: .8125rem;
        }

        @include mq($until: mobile-376) {
            padding: .1rem .5rem;
        }
    }

    >p:last-child {
        @include font-style(1.25rem, rgb(51, 89, 211), $line-height-lg, $font-weight-normal, $font-spacing: 2px);
        display: inline-block;
        padding-left: .5rem;

        @include mq($until: desktop) {
            @include font-style(1rem, rgb(51, 89, 211), $line-height-lg * 1.5, $font-weight-bold, $font-spacing: 2px);
        }

        @include mq($until: leptop-769) {
            @include font-style(1.25rem, rgb(51, 89, 211), $line-height-lg, $font-weight-normal, $font-spacing: 2px);
        }

        @include mq($until: mobile-415) {
            @include font-style(1rem, rgb(51, 89, 211), $line-height-lg * 1.5, $font-weight-bold, $font-spacing: 0px);
        }

        @include mq($until: mobile) {
            font-size: .8125rem;
        }
    }


}

.is-feuture-info-3 {
    margin-top: 2rem;

    // icon with wording
    >p {
        @include background("../img/icon_check.png", top left, no-repeat, 32px);
        padding-left: 2.5rem;
        @include font-style(1rem, rgb(82, 101, 139), $line-height-lg * 1.3, $font-weight-normal, $font-spacing: 2px);
        margin: 1rem 1rem 1rem 0;

        @include mq($until: wide-1081) {
            margin: .5em 1rem .5rem 0;
            background-size: 28px;
            line-height: 1.7;
        }

        @include mq($until: desktop) {
            margin: .5em 1rem .5rem 0;
            background-size: 24px;
            font-size: .875rem;
        }

        @include mq($until: leptop-769) {
            @include background("../img/icon_check.png", top left, no-repeat, 32px);
            @include font-style(1rem, rgb(82, 101, 139), $line-height-lg * 1.3, $font-weight-normal, $font-spacing: 2px);
            margin: 1rem 1rem 1rem 0;
        }

        @include mq($until: mobile-541) {
            margin: .5em 1rem .5rem 0;
            background-size: 24px;
        }

        @include mq($until: mobile-415) {
            margin: 1em 0rem 1rem 0;
            line-height: 1.6;
            padding-left: 2rem;
        }

        @include mq($until: mobile) {
            margin: .5em 0rem .5rem 0;
            background-size: 24px;
            font-size: .875rem;
        }

    }
}

.is-feuture-info-4 {
    max-width: 26rem;
    margin-top: 3rem;

    @include mq($until: wide-1081) {
        margin-top: 2rem;
    }

    @include mq($until: desktop-841) {
        // padding-right: 1rem;
    }


    @include mq($until: leptop-769) {
        margin-top: 2rem;
    }

    @include mq($until: mobile-491) {
        padding-right: 0rem;
    }

    @include mq($until: mobile-415) {
        margin-bottom: 5rem;
    }

    .box {
        float: left;
        width: 50%;
        padding: 0 0;

        // padding: 0 .5rem;//如果有其他btn需要用到

        @include mq($until: mobile-491) {
            padding-right: 0rem;
        }

        @include mq($until: mobile-415) {
            float: none;
            width: 100%;
            padding: 0rem;
        }
    }
}

.info-4-btn {
    width: 100%;
    max-width: 12rem;

    min-height: 3rem;
    position: relative;
    cursor: pointer;

    @include mq($until: desktop-815) {
        max-width: 11rem;
    }

    @include mq($until: leptop-769) {
        max-width: 12rem;
    }

    @include gradient(106deg, rgb(11, 48, 119), rgb(29, 72, 198));
    @include border-radius(3rem);

    &.info-4-btn-right {
        @include gradient(285deg, rgb(87, 162, 225), rgb(44, 105, 191));
    }

    >img {
        position: absolute;
        top: 0;
        z-index: 3;
        height: 2rem;
        top: 50%;
        right: 3%;
        transform: translateY(-50%);

    }

    >a {
        position: absolute;
        top: 50%;
        left: 30%;


        transform: translateY(-30% -50%);
        z-index: 2;
        display: block;
        @include font-style(1rem, #fff, 0, $font-weight-normal, 1);
        // @include links(#fff, #fff, saturate(#fff, 40%), saturate(#fff, 40%));
        @include shift($property: all, $duration: .3s, $ease: ease-in);
        @include box_shadow_gMaterial (1);

        &:hover {
            @include font-style(1rem, #fff795, 0, $font-weight-normal, 1);
            @include shift($duration: .2s);
            @include box_shadow_gMaterial (3);
        }
    }

}