.e-form {
    position: relative;
    top: -20%;
    height: 510px;

    @include mq($until: desktop-841) {
        height: 450px;
    }

    @include mq($until: mobile-541) {
        height: 400px;
    }

    @include mq($until: mobile) {
        height: 370px;
    }

    z-index: 99999;
    @include border-radius(.5rem);
    @include box_shadow_gMaterial (2);
}

.e-form-body {
    position: absolute;
    z-index: -1000;
    width: 50%;
}

.e-form {
    >:first-child {
        width: 50%;

        @include mq($until: desktop-841) {
            width: 100%;
        }

        position: absolute;
        top: 0;
        left: 0;
        height: 510px;

        @include mq($until: desktop-841) {
            height: 450px;
        }

        @include border-radius(.5rem 0rem 0rem .5rem);

        @include mq($until: desktop-841) {
            @include border-radius(.5rem .5rem 0rem 0rem);
        }

        @include mq($until: mobile-491) {
            @include border-radius(0rem 0rem 0rem 0rem);
        }

        @include gradient(158deg, rgba(24, 65, 174, 0.95), rgba(54, 89, 213, 0.95)) {
            @include mq($until: mobile-491) {
                background: rgba(54, 89, 213, 1);

            }

        }
    }

    >:last-child {
        width: 50%;
        width: 50%;

        @include mq($until: desktop-841) {
            width: 100%;
        }

        position: absolute;
        top: 0;

        @include mq($until: desktop-841) {
            top: 100%;
        }

        right: 0;
        @include border-radius(0rem .5rem .5rem 0rem);

        @include mq($until: desktop-841) {
            @include border-radius(0rem 0rem .5rem .5rem);
        }

        @include mq($until: mobile-491) {
            @include border-radius(0rem 0rem 0rem 0rem);
        }

        @include background("../img/img_eform_bg.png", top right, no-repeat, 100%);
        background-color: #1D3286;
        height: 510px;

        @include mq($until: desktop-841) {
            height: 600px;
        }

        @include mq($until: tablet) {
            height: 620px;
        }

        @include mq($until: mobile-541) {
            height: 680px;
        }

        @include mq($until: mobile) {
            height: 585px;
        }
    }
}

