//checkbox style

.checkbox-style {
    display: block;
    // position: relative;
    padding-left: 12px;
    margin-bottom: 12px;
    cursor: pointer;
    user-select: none;
    text-align: left;

    @include font-style(.75rem, rgb(219, 227, 241), $line-height-base, $font-weight-light, 0) {
        @include mq($until: desktop-841) {
            font-size: .875rem;
        }

    }
}

/* Hide the browser's default checkbox */
.checkbox-style input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 1rem;
    width: 1rem;
    border: solid 1px rgb(161, 174, 255);
}

/* On mouse-over, add a grey background color */
.checkbox-style:hover {
    input {
        ~.checkmark {
            background-color: #1d378b;
            border: solid 1px rgb(161, 174, 255);
        }
    }
}

/* When the checkbox is checked, add a blue background */
.checkbox-style {
    input:checked {
        ~.checkmark {
            border: solid 1px rgb(161, 174, 255);
        }
    }
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: block;
}

/* Show the checkmark when checked */
.checkbox-style {
    >input:checked {
        ~.checkmark:after {
            display: none;
        }
    }
}

