* {
    box-sizing: border-box;
    margin: 0 auto;
    font-family: $font-family-base-tc; //$font-family-base
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: baseline;
    background: 0 0;
    padding-left: 0px;
    padding-right: 0px;

    // &::before,
    // ::after {
    //     box-sizing: inherit;
    // }

    // fixed bootstrap 3 form
}

a {
    display: inline-block;
    vertical-align: middle;
    text-decoration: none !important;
}

p {
    margin-bottom: 0rem;
}

ul {
    margin-bottom: 0rem;

    >li {
        list-style-type: none;
    }
}

input:focus {
    outline: none;
}

// h1,
// h2,
// h3,
// h4,
// h5,
// h6,
// p,
// a {
//     letter-spacing: 1px;
// }

::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(140, 151, 229) !important;
    opacity: 1;
    /* Firefox */
}

::-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: rgb(140, 151, 229) !important;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: rgb(140, 151, 229) !important;
}

textarea:focus,
input:focus {
    color: rgb(255, 247, 149) !important;
}

.col-xs-12,
.col-xs-3,
label {
    padding-left: 0px !important;
    padding-right: 0px !important;
}

select option {
    color: black !important;
    -webkit-appearance: none;
}