.service-wrap {
    width: 100%;
    @include gradient(180deg, rgb(39, 45, 145), rgb(29, 72, 198));
    min-height: 1200px;
    overflow: hidden;

    >.service-wrap-inner {
        background-image: url(../img/img_bg_service.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 90%;

        @include mq($until: leptop-769) {
            background-position: 0 129px;
            background-size: 100%;
        }

        @include mq($until: mobile-415) {
            background-position: 0 120px;
        }

        >.service-content {
            background-image: url(../img/img_bg_triangle_bottom.png);
            background-repeat: no-repeat;
            // background-position-y: -1.25rem;

            @include mq($until: mobile-415) {
                background-position-y: -3px;
            }

            background-size: 100%;
        }
    }
}

.service-container {
    width: 100%;
    max-width: 1336px;
    margin: 0 auto; // border: 1px solid red;
    margin: 0 2rem;
    margin: 0 auto;
}

.service-logo {
    width: inherit;
    height: auto;
    text-align: center;
    padding-top: 30%;

    @include mq($until: wide-1281) {
        padding-top: 18%;
    }

    @include mq($until: wide-1115) {
        padding-top: 18%;
    }


    @include mq($until: desktop-1025) {
        padding-top: 14%;
    }

    @include mq($until: leptop-780) {
        padding-top: 20%;
    }

    @include mq($until: mobile-592) {
        padding-top: 14%;
    }

    @include mq($until: mobile-415) {
        padding-top: 20%;
    }


    >img {
        height: 121px;

        @include mq($until: mobile-415) {
            height: 90px;
        }
    }
}

.service-content-inner {
    text-align: center;

    margin-top: 20rem;

    @include mq($until: wide-1980) {
        margin-top: 2rem;
    }

    @include mq($until: desktop-877) {
        margin-top: 2rem;
    }

    @include mq($until: mobile-415) {
        margin-top: 0rem;
    }


    padding: 0 2rem;

    @include mq($until: mobile-415) {
        padding: 0 0rem;
    }

    >h4 {
        @include font-style(2.25rem, #fff, $line-height-lg * 2.5, $font-weight-bold, 2);

        @include mq($until: desktop-877) {
            font-size: 2rem;
        }

        @include mq($until: mobile-592) {
            font-size: 1.75rem;
        }

        @include mq($until: mobile-541) {
            font-size: 1.5rem;
            padding: 1rem 2rem;
            line-height: 1.8
        }

        @include mq($until: mobile-415) {
            font-size: 1.375rem;
            padding: 1rem .5rem;
            line-height: 1.8
        }
    }
}

.is-service-table {
    width: 100%;
    max-width: 1136px;
    margin: 0 auto;
    padding: .5rem;
    background: rgb(39, 45, 145);
    @include border-radius(.5rem);

    >.is-service-table-box {
        background: rgb(188, 216, 255);
        text-align: left;
        border-bottom: .5rem solid rgb(39, 45, 145);
        @include clearfix();

        >.table-box {
            float: left;

            @include mq($until: leptop-769) {
                float: none;
            }

            &.table-box-sub {
                width: 30%;
                text-align: left;
                background: rgb(188, 216, 255);
                padding: 2rem;
                text-align: left;

                @include mq($until: leptop-769) {
                    width: 100%;
                    padding: 1rem 0;
                    height: 4rem;
                    text-align: center;
                    min-height: 0rem;
                }
            }

            &.table-box-list {
                width: calc(100% - 30%);

                @include mq($until: leptop-769) {
                    width: 100%;
                }

                @include mq($until: leptop-641) {
                    text-align: center;
                }

                background: rgb(55, 90, 214);
            }
        }
    }

    .table-box-4 {
        border-bottom: none;
    }
}

.table-box-list {
    @include clearfix();
    background: rgb(2, 58, 167);

    >.is-table-box {
        float: left;
        width: 40%;

        @include mq($until: desktop-841) {
            width: 45%;
        }

        @include mq($until: leptop-641) {
            float: none;
            width: 100%;
        }

        padding: 2rem;

        @include mq($until: mobile-415) {
            padding: 2rem 1rem 1.5rem 1rem;
        }

        @include mq($until: mobile-415) {
            min-height: 0rem;
        }

        &.table-box-L {


            @include mq($until: mobile-415) {

                padding: 1rem 1rem 1.5rem 1rem;
            }

            background-image: url(../img/icon_arrow_light.png);

            @include mq($until: leptop-769) {
                background-image: none;
            }

            background-position: left;
            background-repeat: no-repeat;
            background-size: 16px;
        }

        &.table-box-R {
            float: right;
            width: 60%;

            @include mq($until: desktop-841) {
                width: 55%;
            }

            @include mq($until: leptop-641) {
                float: none;
                width: 100%;
            }

            background: rgb(2, 58, 167);
            background-image: url(../img/icon_arrow_dark.png);
            background-position: left;
            background-repeat: no-repeat;
            background-size: 16px;

            @include mq($until: leptop-641) {
                background-image: url(../img/icon_arrow_dark_bottom.png);
                background-position: top;
                background-size: 48px;
            }
        }
    }
}

.table-box-sub {
    position: relative;

    >p {
        @include font-style(1.125rem, rgb(33, 76, 161), $line-height-lg, $font-weight-bold, 1) {
            padding-bottom: 2rem
        }
    }
}

.is-table-box {
    >p {
        @include font-style(.875rem, #fff, $line-height-lg * 1.2, $font-weight-light);

        @include mq($until: leptop-641) {
            font-size: 1rem;
        }

        @include mq($until: mobile-415) {
            font-size: .875rem;
        }
    }
}

.table-box-4 {
    @include clearfix();
    background: #fff;
    text-align: left;

    @include mq($until: mobile-592) {
        padding: 1.5rem 0;
    }



    >.service-rights {
        float: left;
        width: calc(100% - 30% * 1.4);


        background: #fff;
        @include clearfix();

        @include mq($until: desktop-894) {
            width: 40%;
        }

        @include mq($until: leptop-728) {
            width: 45%;
        }

        @include mq($until: leptop-649) {
            width: 100%;
        }

        @include mq($until: mobile-592) {
            width: 100%;

        }
    }

    >.service-photo {
        float: right; // width: 45%;

        @include mq($until: leptop-649) {
            display: none;
        }
    }


}

.service-rights-box {
    float: left;
    width: 50%;
    padding: 2rem;

    @include mq($until: wide-1115) {
        padding: 2rem 1rem;

        @include mq($until: desktop-894) {
            padding: 1rem 1rem;
        }
    }

    @include mq($until: desktop-894) {
        float: none;
        width: 100%;
    }

    @include mq($until: leptop-649) {

        float: left;
        width: 50%;
    }

    @include mq($until: mobile-592) {
        padding: 0;
        float: none;
        width: 100%;
    }

    @include mq($until: mobile-592) {
        text-align: center;
    }



    p.rights-title {
        @include font-style(1.125rem, rgb(0, 166, 255), $line-height-lg * 2, $font-weight-bold, 2);

        @include mq($until: desktop) {
            font-size: 1rem;
        }
    }

    p.rights-list {
        @include background("../img/icon_check_dark.png", top left, no-repeat, 32px);
        padding-left: 40px;
        @include font-style(1rem, rgb(82, 101, 139), $line-height-lg * 1.25, $font-weight-normal, $font-spacing: 2px);
        margin: 1rem 1rem 1rem 0;

        @include mq($until: wide-1115) {
            margin: .5em 1rem .5rem 0;
            background-size: 24px;
            font-size: .875rem;
            padding-left: 1.75rem;
        }

        @include mq($until: desktop-894) {
            font-size: 1rem;
        }

        @include mq($until: mobile-592) {
            background: none;
        }

    }
}

.service-photo {
    //    @include background("../img/img_photo.png");
    width: calc(100% - (100% - 30% * 1.4));


    background-image: url(../img/img_photo.png);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    height: 400px;

    @include mq($until: wide-1115) {
        height: 300px;
    }

    @include mq($until: desktop-894) {
        height: 550px;
        width: 60%;

        @include mq($until: leptop-728) {
            width: 55%;
        }
    }
}

.btn-reservation {
    margin: 4rem 2rem;

    @include mq($until: mobile-415) {
        margin: 4rem 0rem;
    }

    a {
        display: inline-block;
        @include button-bg(rgb(79, 172, 254));
        @include links(#fff, #fff, saturate(#fff795, 40%), saturate(#fff795, 40%));
        @include border-radius(3rem);

        @include font-style(1.5rem, #fff, 1.6, $font-weight-bold, 1);

        @include mq($until: mobile-415) {
            font-size: 1.25rem;
        }

        @include mq($until: mobile-376) {
            font-size: 1rem;
        }

        padding: 1rem 5.8rem;

        @include mq($until: mobile-415) {
            padding: 1rem 7rem;
        }

        @include mq($until: mobile-376) {
            padding: 1rem 5.5rem;
        }

        @include box_shadow_gMaterial (1);
        @include shift($property: all, $duration: .3s, $ease: ease-in);

        &:hover {
            @include shift($duration: .2s);
            @include button-bg(saturate(rgb(0, 242, 254), 60%));
            @include box_shadow_gMaterial (3);
            padding: 1rem 6rem;

            @include mq($until: mobile-415) {
                padding: 1.2rem 7.1rem;
            }

            @include mq($until: mobile-376) {
                padding: 1.2em 5.7rem;
            }


        }
    }
}