.prdouct-wrap {
    width: 100%;
    @include background("../img/img_bg_triangle.png", top, no-repeat, 100%);
    margin-bottom: 10%;

    @include mq($until: desktop) {
        margin-top: -9rem;
    }

    @include mq($until: leptop-769) {
        margin-top: 2rem;
    }

    @include mq($until: leptop-728) {
        margin-top: 6rem;
        @include background("../img/img_bg_triangle.png", top, no-repeat, 100%);
    }

    @include mq($until:tablet) {
        margin-top: 4rem;
    }

    @include mq($until: mobile-541) {
        margin-top: 2rem;
    }

    @include mq($until: mobile-415) {
        margin-top: 1rem;
    }

    >.is-prdouct-wrap {
        margin: auto;
        max-width: 1136px;
        padding: 0 2rem;

        @include mq($until: mobile-415) {
            padding: 0 1rem;
        }

        &.prdouct-sub {
            padding-bottom: 3rem;
            padding-top: 10rem;

            @include mq($until: leptop-728) {
                padding-top: 9rem;
            }

            @include mq($until: mobile-541) {
                padding-top: 7rem;
            }

            @include mq($until: mobile-491) {
                padding-bottom: 0rem;
            }

            @include mq($until: mobile-376) {
                padding-top: 5rem;
            }

            p {
                text-align: center;
            }
        }
    }
}

.prdouct-sub {
    color: rgb(33, 76, 161);

    >:nth-child(1) {
        @include font-style(2.25rem, rgb(33, 76, 161), $line-height-lg, $font-weight-bold, $font-spacing: 2px);

        @include mq($until: desktop) {
            font-size: 1.75rem;
        }

        @include mq($until: mobile-541) {
            font-size: 1.5rem;
        }

        @include mq($until: mobile-491) {
            font-size: 1.25rem;
        }

        @include mq($until: mobile-376) {
            font-size: 1rem;
        }
    }

    >:nth-child(2) {
        @include font-style(2.25rem, rgb(33, 76, 161), $line-height-lg, $font-weight-bold, $font-spacing: 2px);

        @include mq($until: desktop) {
            font-size: 1.75rem;
        }

        @include mq($until: mobile-491) {
            font-size: 1.55rem;
        }

        @include mq($until: mobile-415) {
            font-size: 1.375rem;
        }

        @include mq($until: mobile-376) {
            font-size: 1.25rem;
        }

        @include mq($until: mobile) {
            font-size: 1rem;
        }
    }

    >:nth-child(3) {
        @include font-style(1.5rem, rgb(33, 76, 161), $line-height-lg * 2, $font-weight-light, $font-spacing: 2px);

        @include mq($until: desktop) {
            font-size: 1.35rem;
        }

        @include mq($until: leptop-769) {
            font-size: 1.275rem;
        }

        @include mq($until: leptop-728) {
            font-size: 1.275rem;
            line-height: $line-height-lg;
            padding-top: 1.5rem;
        }

        @include mq($until: mobile-541) {
            font-size: 1.125rem;
            padding-top: 1rem;
        }

        @include mq($until: mobile-376) {
            font-size: 1rem;
        }

        @include mq($until: mobile) {
            font-size: .875rem;
        }
    }
}

.prdouct-line {
    width: 100%;
    max-width: 1136px;
    margin: 0 auto;
    display: block;

    @include mq($until: leptop-728) {
        display: none;
    }

    >.is-prdouct-line {
        @include background("../img/img_pdline.png", center, no-repeat, 100%);
        min-height: 182px;
        font-size: 0;

        >.is-prdouct-box {
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            width: 25%;
            // border: 1px solid red;
        }
    }
}

.prdouct-line-top {
    @include font-style(.875rem, rgb(82, 101, 139), $line-height-lg, $font-weight-light, $font-spacing: 0);

    @include mq($until: desktop) {
        letter-spacing: 0 !important;
    }

    @include mq($until: leptop-780) {
        font-size: .8125rem;
    }
}

.prdouct-line-bottom {
    @include font-style(1.125rem, rgb(33, 76, 161), $line-height-lg, $font-weight-bold, $font-spacing: 0);

    @include mq($until: desktop) {
        font-size: 1rem;
    }

    @include mq($until: desktop-877) {
        font-size: .875rem;
    }

    @include mq($until: leptop-780) {
        font-size: .8125rem;
    }
}

.is-prdouct-line {

    >.is-prdouct-box {
        >p {
            display: block;
        }
    }

    .prdouct-line-top {
        // border: 1px solid red;
    }

    .prdouct-line-bottom {
        // border: 1px solid red;
    }
}

.prdouct-line-a,
.prdouct-line-d {
    >.prdouct-line-top {
        padding-bottom: 2rem;
    }

    >.prdouct-line-bottom {
        margin-top: 10rem;
    }
}

.prdouct-line-b,
.prdouct-line-c {
    >.prdouct-line-top {
        margin-top: 3rem;
    }

    >.prdouct-line-bottom {
        margin-top: 9rem;
    }
}