// body set
.page-home {
    max-width: 2599px;
    margin-left: auto;
    margin-right: auto;

    background: #F6FAFC;
}

// main content

.video-wrap {
    width: 100%;
    height: 28rem;

    @include mq($until: desktop) {
        height: 26rem;
    }

    @include mq($until: mobile-491) {
        height: 25rem;
    }

    @include mq($until: mobile) {
        height: 24rem;
    }

    margin: 0;
    padding: 0;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    -webkit-tap-highlight-color: transparent;
}

.bg-wrap {
    z-index: 1;
    position: relative;
    top: 40%;
    width: 100%;
    background: #F6FAFC;
    height: 100%;
    padding: 0 2rem !important;
}

.eform-warp {
    z-index: 99;
    position: absolute;
    width: 100%;
    height: auto;
    margin-top: -18.75rem;

    @include mq($until: wide-1081) {
        margin-top: -11.75rem;
    }
}

.inner-content-mg {
    margin: 0 2rem;

    @include mq($until: leptop-769) {
        margin: 0 1rem;
    }
}

.inner-content-pd {
    padding: 0 2rem;
}

.inner-content {
    padding: 0 2rem;

    @include mq($until: desktop-841) {
        padding: 0 5rem;
    }

    @include mq($until: leptop-641) {
        padding: 0 2rem;
    }

    @include mq($until: mobile-491) {
        padding: 0 1rem;
    }

    @include mq($until: mobile-491) {
        padding: 0 0rem;
    }
}

.section {
    width: 100%;
    margin: 0 auto;
    max-width: 1136px;
}