.about-warp {
    overflow: hidden;
    height: 100%;
    top: 55%;
    min-height: 700px;

    @include mq($until: desktop-841) {
        margin-top: 40rem;
    }

    .all-layer {
        width: 100%;
        position: relative;

        &.about-layer-1 {
            z-index: -2;
        }

        &.about-layer-2 {
            z-index: -1;
        }

        &.about-layer-3 {
            z-index: 1;
        }
    }
}

.about-layer-1 {
    >img:first-child {
        top: 15rem;
        left: -5%;
        position: absolute;

        @include mq($until: mobile-415) {
            top: 21rem;
            left: 53%;
        }
    }

    >img:last-child {
        top: 0;
        right: -5%;
        position: absolute;

        @include mq($until: mobile-415) {
            top: 0;
            right: 36%;
        }
    }
}

.about-layer-2 {
    >img:first-child {
        top: 32rem;
        left: -2%;
        position: absolute;
        width: 8%;
        @include keyframe(alternate);
    }

    >img:last-child {
        top: 6rem;
        right: -3%;
        position: absolute;
        width: 8%;
        @include keyframe(alternate-reverse);
    }
}

.about-layer-3 {
    max-width: 1136px;
    margin: auto;
    margin-top: 8%;
    @include clearfix();

    @include mq($until: leptop-769) {
        margin-top: 20%;
    }

    @include mq($until: mobile-541) {
        margin-top: 27%;
    }

    @include mq($until: mobile-491) {
        margin-top: 32%;
    }

    @include mq($until: mobile-415) {
        margin-top: 40%;
    }

    @include mq($until: mobile) {
        margin-top: 20%;
    }

    >.is-about-content {
        float: left;
        width: 50%;

        @include mq($until: leptop-769) {
            float: none;
            width: 100%;
            margin-top: 36rem;
        }

        @include mq($until: leptop-641) {
            margin-top: 32rem;
        }

        @include mq($until: mobile-541) {
            margin-top: 28rem;
        }

        @include mq($until: mobile-491) {
            margin-top: 25rem;
        }

        @include mq($until: mobile-376) {
            margin-top: 27rem;
        }

        @include mq($until: mobile) {
            margin-top: 24rem;
        }
    }

    >.about-layer-content.about-3-bottompic {
        float: left;
        width: 50%;

        @include mq($until: leptop-769) {
            float: none;
            width: 100%;
        }

    }
}

// 多物件的垂直置中
// Line-height + inline-block
.is-about-content {
    width: 100%;
    margin: auto;
    line-height: 8rem;
    text-align: center;

    >.is-about-content-box {
        text-align: left;
        display: inline-block;
        height: auto;
        line-height: 1;
        width: 80%;
        vertical-align: middle;

        @include mq($until: leptop-769) {
            width: 90%;
        }
    }

}

.is-about-content-box {
    >h3 {
        @include font-style(2.5rem, rgb(13, 67, 169), $line-height-lg, $font-weight-bold, $font-spacing: 2px);

        @include mq($until: wide-1081) {
            font-size: 2rem;
        }

        @include mq($until: desktop) {
            font-size: 1.75rem;
        }

        @include mq($until: desktop-841) {
            font-size: 1.5rem;
        }

        @include mq($until: leptop-769) {
            font-size: 1.75rem;
            text-align: center;
        }

        @include mq($until: mobile-491) {
            font-size: 1.55rem;
        }

        @include mq($until: mobile-415) {
            font-size: 1.375rem;
        }

        @include mq($until: mobile-376) {
            font-size: 1.25rem;
        }

        @include mq($until: mobile) {
            font-size: 1rem;
        }

        margin: 2rem 0;

        @include mq($until: mobile) {
            margin: 1rem 0;
        }

    }

    >p {
        @include font-style(1rem, rgb(82, 101, 139), $line-height-lg * 1.4, $font-weight-light, $font-spacing: 2px) {
            text-align: justify;
        }

        @include mq($until: desktop) {
            font-size: .875rem;
        }

        @include mq($until: leptop-769) {
            font-size: 1rem;
        }

        @include mq($until: mobile) {
            font-size: .875rem;
        }
    }
}

.is-about-content-kv {
    width: 100%;
    position: relative;
    height: auto;

    >img {
        width: 90%;
        height: auto;
        position: absolute;
        top: -1rem;
        right: 0;
        @include keyframe(alternate);

        @include mq($until: desktop) {
            top: 1rem;
        }

        @include mq($until: leptop-769) {
            width: 70%;
            top: 1rem;
            right: 5rem;
        }

        @include mq($until: mobile-491) {
            width: 75%;
            right: 3rem;
        }

        @include mq($until: mobile-415) {
            width: 85%;
            right: 1.5rem;
        }

        @include mq($until: mobile-376) {
            width: 100%;
            right: 0rem;
        }
    }

    >p {
        @include font-style(1rem, rgb(0, 166, 255), $line-height-lg, $font-weight-bold, $font-spacing: 2px);
        position: absolute;

        &.feature-1 {
            top: 12rem;
            left: -2rem;

            @include mq($until: leptop-769) {
                top: 16rem;
                left: 1rem;
            }

            @include mq($until: leptop-641) {
                top: 14rem;
            }

            @include mq($until: mobile-541) {
                top: 12rem;
            }

            @include mq($until: mobile-376) {
                top: 11rem;
                left: 0rem;
            }

        }

        &.feature-2 {
            color: rgb(74, 144, 226);
            top: 30rem;
            right: 8rem;

            @include mq($until: wide-1081) {
                top: 27rem;
                right: 7rem;
            }

            @include mq($until: desktop) {
                top: 26rem;
            }

            @include mq($until: desktop-841) {
                top: 22rem;
                right: 4rem;
            }

            @include mq($until: leptop-769) {
                top: 33rem;
                right: 13rem;
            }

            @include mq($until: leptop-641) {
                top: 28rem;
                right: 10rem;
            }

            @include mq($until: mobile-541) {
                top: 22rem;
                right: 6rem;
            }

            @include mq($until: mobile-491) {
                top: 20rem;
                right: 4rem;
            }

            @include mq($until: mobile-415) {
                top: 22rem;
            }

            @include mq($until: mobile-415) {
                top: 23rem;
            }

            @include mq($until: mobile) {
                top: 20rem;
            }
        }

        &.feature-3 {
            color: rgb(33, 76, 161);
            top: -1rem;
            right: 0rem;

            @include mq($until: leptop-769) {
                top: 0rem;
                right: 8rem;
            }

            @include mq($until: leptop-641) {
                top: 0rem;
                right: 8rem;
            }

            @include mq($until: mobile-541) {
                right: 3rem;
            }
        }
    }
}


.about-layer-content.about-3-toppic {
    display: none;

    @include mq($until: leptop-769) {
        display: block;
    }
}

.about-layer-content.about-3-bottompic {
    display: block;
    min-height: 200px;

    @include mq($until: leptop-769) {
        display: none;
    }
}