// FONT -- Main colors
$color-primary: #52658B;
$color-secendary: #214CA1;
$color-tertiary: #879CC5;
$color-white: #ffffff;
$color-dark: #002242;
$color-warning: #F48A48;
$color-danger: #A70040;
$color-powderBlue: #CCD9E3;
$color-highlight: #4FACFE;
$color-lightblue: #E1ECF4;

// Font -- base -- link-color
$font-link-normal: $color-dark;
$font-link-visit: $color-dark;
$font-link-hover: $color-secendary;
$font-link-active: $color-secendary;


// Background color -- Main color
$color-bg-primary: lighten($color-white, 20%);
$color-bg-secendary: #2159C6;
$color-bg-tertiary : #DBE3F1;
$color-bg-white: $color-white;
$color-bg-mariner: darken($color-bg-secendary, 30%); //
$color-bg-blackSqueeze: lighten($color-bg-tertiary, 30%); //more 新增選項背景色
$color-bg-powderBlue: $color-powderBlue;



// Background color -- tab
$color-periwinkle: #C7DDFF;
$color-pattensBlue: #d6e6ff;
$color-zumthor: #e9f2ff;
$color-zircon: #F5F9FF;



// Background color -- button 按鈕顏色狀態
$color-royalBlue: #465AE1;
$color-funBlue: #214CA1;
$color-cornflowerBlue: #6ea1f2;
$color-danube: #5E89CC;



// Background color -- Form
$color-aliceBlue:#F3FAFF;


//Background Image -- Image


// Background color -- Main color -- gradient
$gradient-primary: linear-gradient(157deg, #2868ca, #5296e8 52%, #6eb2f9);
$gradient-secendary: linear-gradient(117deg, $color-bg-secendary, #455ae1);

// Theam & Dialog
$theam-bgcolor: $gradient-primary !default;
$dialog-bgcolor: $gradient-secendary;

// container backgroung color 內容背景色
$content-bgcolor: $color-bg-primary;

// devider 分隔線
$devider-color-horizontal: $color-bg-tertiary;
$devider-color-horizontal-short: $color-bg-powderBlue;
$devider-color-vertical: $color-bg-powderBlue; //垂直分隔線

// Subtitle
$subtitle-fontColor: $color-primary;


// button
$button-color-standard-normal: $color-royalBlue;
$button-color-standard-active: $color-funBlue;
$button-color-large-normal: $color-cornflowerBlue;
$button-color-large-active: $color-danube;



// KVB Font family

// Fonts
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif: "Noto Sans SC",
"Noto Sans TC",
"Roboto Condensed",
"Roboto",
Arial,
Helvetica,
sans-serif,
微軟正黑體,
"Segoe UI",
"Helvetica Neue",
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;
$font-family-sans-serif-tc: "Roboto Condensed",
"Roboto",
Arial,
Helvetica,
sans-serif,
微軟正黑體,
"Segoe UI",
"Helvetica Neue",
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;
$font-family-base: $font-family-sans-serif !default;
$font-family-base-tc: $font-family-sans-serif-tc !default; //繁體字型
// stylelint-enable value-keyword-case

$font-size-base: 1rem !default; // Assumes the browser default, typically `16px`
$font-size: 16px !default;

$font-size-lg: ($font-size-base * 1.25) !default;
$font-size-sm: ($font-size-base * .875) !default;

$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-bold: 500 !default;

$font-weight-base: $font-weight-normal !default;
$line-height-base: 1.5 !default;

$h1-font-size: $font-size-base * 2.5 !default;
$h2-font-size: $font-size-base * 2 !default;
$h3-font-size: $font-size-base * 1.75 !default;
$h4-font-size: $font-size-base * 1.5 !default;
$h5-font-size: $font-size-base * 1.25 !default;
$h6-font-size: $font-size-base !default;

// $headings-margin-bottom:      ($spacer / 2) !default;
$headings-font-family: inherit !default;
$headings-font-weight: 500 !default;
$headings-line-height: 1.2 !default;
$headings-color: inherit !default;

$display1-size: 3.25rem !default;
$display2-size: 2rem !default;
// $display3-size:               4.5rem !default;
// $display4-size:               3.5rem !default;

$display1-weight: 300 !default;
$display2-weight: 300 !default;
// $display3-weight:             300 !default;
// $display4-weight:             300 !default;
$display-line-height: $headings-line-height !default;

$small-font-size: 80% !default;

// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg: 1.5 !default;
$line-height-sm: 1.5 !default;

$border-width: 1px !default;
$border-color: $color-bg-tertiary !default;

$border-radius: .25rem !default;
$border-radius-lg: .3rem !default;
$border-radius-sm: .2rem !default;

$box--sm: 0 .125rem .25rem rgba($color-dark, .075) !default;
$box-: 0 .5rem 1rem rgba($color-dark, .15) !default;
$box--lg: 0 1rem 3rem rgba($color-dark, .175) !default;

$component-active-color: $color-white !default;
$component-active-bg: rgba($color-primary, 1) !default;

$caret-width: .3em !default;

// $transition-base:             all .2s ease-in-out !default;
// $transition-fade:             opacity .15s linear !default;
// $transition-collapse:         height .35s ease !default;

//  Options
$enable-rounded: true !default;
// Spacing
//

$spacer: 1rem !default;
// $spacers: () !default;

// Border - radius
$border-radius: .25rem !default;
$border-radius-lg: .3rem !default;
$border-radius-sm: .2rem !default;


// Shape - shape size
$shape-size: 50rem;

// Block padding/margin
$wrap-padding-base: 3rem;
$wrap-margin-base: 3rem;

// Content Position
//
$width-base:100% !default;

//  number

$number-base: 1;
$percent: "%";