.video-container {
    position: relative;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: -1000000;
}

video#bgvideo {
    position: relative;
    width: 100%;
    height: auto;
    z-index: -100;
    background-size: cover;
}

.pattern-swatch {
    background-image: url(../img/pattern_dot.png);
    background-repeat: repeat;
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: auto;
    opacity: 0.25;
}

#overlay {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: auto;
    background-color: rgba(0, 128, 234, 0.3);
    z-index: 2147483647;
}

.hideOverflow {
    overflow: hidden;
}

// video none

.video-container {
    display: block;

    @include mq($until: desktop-815) {
        display: none;
    }
}

.video-none {
    display: none;
    //  @include background("../img/kvb.png",center, no-repeat,cover){
    background-image: url("../img/video_img.png");
    background-position-y: 66px;
    background-repeat: no-repeat;
    background-size: 120%;
    min-height: 70rem;


    @include mq($until: desktop-815) {
        display: block;
    }

    @include mq($until: mobile-415) {
        background-size: 125%;
        background-position-x: -4rem;

    }

    @include mq($until: mobile-376) {
        background-size: 140%;
        background-position-x: -5rem;

    }

}