.footer{
    // padding: 2rem 1.6rem 1.6rem 1.6rem;
    // @include gradient(135deg, #2868CA, #6EB2F9);
    background: #1d48c6;

}
.disclaimer{
     padding: 3rem 1.6rem;
     @include divider(top, 2px, solid, rgba(204, 217, 227, 0.2));
}
.disclaimer-inner {
    // max-width: 1125px;
    > h4{
        @include font-style(.875rem, rgb(135, 156, 197), $line-height-lg, $font-weight-normal, 0rem);
        padding-bottom: 1rem;
    }
    > p{
        @include font-style(.8125rem, rgb(135, 156, 197), $line-height-lg * 1.25, $font-weight-light, 0rem);
        text-align: justify;
    }
    > h5{
        @include font-style(.75rem, $color-lightblue, $line-height-lg, $font-weight-bold, 0rem);
        padding-top: 1rem;
    }
}
.footer-bottom-inner{
    overflow: hidden;
}
.footer-bottom{
    padding: 1.5rem .8rem 1rem .8rem;
    overflow: hidden;
    background: #0c0f2a;
    @include divider(top, 1px, solid, rgb(11, 15, 42));
    .footer-bottom-inner{
        // max-width: 1125px;
    .footer-bottom-col-left{
        float: left;
        width: 50%;
        }
    }
}
.footer-bottom__logo{
    img{
        width: 50%;
    }
}
.footer-bottom-col-right {
    text-align: right;
    > p{
        display: inline-block;
        @include font-style(.875rem, rgb(129, 144, 155), $line-height-lg, $font-weight-light, 0rem);
        padding-top: 1.5%;
    }
}
@media (max-width: 768px) {
    .footer-bottom-col-right {
        text-align: right;
    }
}
@media (max-width: 655px) {
    .footer-bottom-col-right > p {
        font-size: .875rem;
    }
}
@media (max-width: 580px) {
    .footer-bottom-col-right {
        p{
            font-size: .875rem;
        }
    }
    .footer-bottom .footer-bottom-inner .footer-bottom-col-left {
        float: none;
        width: 100%;
    }
    .footer-bottom-col-right {
        text-align: left;
    }
    .footer-bottom-col-right {
        > p{
            padding-top: 2.5%;
        }
    }
}
@media (max-width: 414px) {
    .footer-bottom__logo img {
        width: 40%;
    }
}