.fixed-wrap {
    position: fixed;
    top: 0;
    z-index: 9999;
    width: 100%;

    .header {
        min-height: 5.5rem;
        background-color: $color-bg-white;
        @include shadow(0px, 2px, 5px, 0px, rgba(0, 0, 0, 0.5));
    }

    .nav {
        >a {
            display: block;

            img {
                height: 2.5rem;
            }
        }

        >* {
            float: left;
        }
    }
}

.kvb-logo {
    margin: 1.375rem 0 0 1rem;
}

.kvb-slogan {
    margin: 1.375rem 1rem 0 0;
}

// 利用 css 讓 ul div 置中
// ul 外還有父層 div
// 那我們可以在父層設定text-align，並修改ul的 display 狀態為 inline-block

.menu-button {
    width: calc(100% - 200px - 180px - 2rem);
    text-align: right;
}

ul.kvb-menu {
    display: inline-block;
    height: 5.5rem;
    top: 25%;
    max-width: 1200px;

    >li {
        float: left;
        display: block;
        height: 5.5rem;
        line-height: 5.5rem;
        margin-right: .625rem;

        >a {
            @include font-style(.875rem, #fff, $line-height-lg, $font-weight-normal, 0);

            @include mq($until: wide-1281) {
                font-size: .8125rem;
            }

            @include links($font-link-normal, $font-link-normal, $font-link-hover, $font-link-hover);

            @include shift($property: all, $duration: .3s, $ease: ease-in);

            &:hover {
                @include shift($property: all, $duration: .3s, $ease: ease-in);
            }
        }

    }
}